.modal {
    height: 100vh;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: scale(0);
    z-index: 1;
    font-family: 'Nunito', sans-serif;

}

.modal.active {
    transform: scale(1);
}



.modalContent {
    padding: 20px;
    border-radius: 12px;
    background-color: rgb(46, 46, 56);
    width: 800px;
    height: 600px;

}

.modalTitle{
    color: #878e99;
    margin-bottom: 30px;
    font-size: 30px;
}

.modalImg{
    margin-bottom: 20px;
    width: 760px;
}

.modalDescription{
    color: #878e99;
}

.modalLink{
    display: inline-block;
    padding: 10px 20px;
    font-size: 18px;
    text-align: center;
    text-decoration: none;
    letter-spacing: 1px;
    background: rgba(0,0,0,0);
    box-shadow: inset 0 0 0 3px #878e99;
    color: #878e99;
    border: none;
    cursor: pointer;
    font-weight: 600;
    position: relative;
    transition: all 500ms ease;
    margin-top: 15px;
}

.modalLink:hover{
    text-decoration: none;
    box-shadow: inset 0 0 0 3px #dc965a;
    background: rgba(0,0,0,0);
    color: #dc965a;
}

@media screen and (max-width: 834px){
    .modalContent{
        width: 620px;
        height: 515px;
    }
}

@media screen and (max-width: 667px){
    .modalContent{
        width: 500px;
        height: 470px;
    }

    .modalLink{
        font-size: 15px;
    }
}

@media screen and (max-width: 568px){
    .modalContent{
        width: 400px;
        height: 400px;
    }

    .modalTitle{
        margin-bottom: 10px;
    }
}

@media screen and (max-width: 414px){
    .modalContent{
        width: 315px;
        height: 380px;
    }
}
